import axios from 'axios';
import React, { useEffect, useState } from 'react';

const unmarshall = (thisElem) => {
  if (Array.isArray(thisElem)) {
    for (const [i,subElem] of thisElem.entries()) {
      thisElem[i] = unmarshall(subElem)
    }
    return thisElem
  } else if (typeof thisElem == "object") {
    for (const prop in thisElem) {
      if (prop.length < 3) {
        return unmarshall(thisElem[prop])
      } else {
      	thisElem[prop] = unmarshall(thisElem[prop])
      }
    }
  }
  return thisElem
}

function SpeciesItem(props) {
  const [data, dataSet] = useState(null)

  const getSpecies = () => {
    axios
      .get("https://2y0eh1tux1.execute-api.sa-east-1.amazonaws.com/default/species")
      .then((response) => {
        console.log(response.data)
        console.log(response.data.Items)
        dataSet(unmarshall(response.data))
      })
  }

  useEffect(() => {
    getSpecies()
  }, [])
  
  console.log(data)
  
  return (
    <div>
      data: 
    </div>
  )
}

export default SpeciesItem;